/*================= Fonts imports  =================*/
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap');

/*================= Predefined styles =================*/
.flex-center {
    display: flex;
    justify-content: center;
    align-items: center;
}

/*================= Variables -  Typos =================*/
$primary-font: 'Roboto', sans-serif;

/*================= Main CSS =================*/
* {
    box-sizing: border-box;
}

body {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    margin: 0;
    padding: 0;
    font-family: $primary-font;
    font-size: 16px;
}

header {
    @extend .flex-center;
    flex-direction: column;
    padding: 1rem;
    background-color: #97ce4c ;
    color: #fff;

    .searchbar {
        width: 40%;
        padding: 1rem;
        border: none;
        outline: none;
        transition: all .3s;

        &:focus {
            color: #fff;
            background-color: #202329;
        }
    }
}

main {
    display: flex;
    justify-content: center;
    flex-grow: 1;
}

section {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 2rem;
}

.characters {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: .5rem;
    padding: 2rem;

    .character-card {
        display: flex;
        flex-direction: column;
        border: 2px solid #333;
        padding: .4rem;
    }

    .character-species {
        font-size: .8rem;
    }
}

footer {
    @extend .flex-center;
    background-color:#202329;
    color: #fff;
}

